import {
  FETCH_PRICE_REQUEST,
  FETCH_PRICE_FAILURE,
  FETCH_PRICE_SUCCESS,
} from "./priceActions";

import { PriceActions, PriceState } from "./priceTypes";

const initialPriceState: PriceState = {
  pending: false,
  error: null,
  price: [],
};

export default (state = initialPriceState, action: PriceActions) => {
  switch (action.type) {
    case FETCH_PRICE_REQUEST:
      return {
        ...state,
        pending: true,
      };
    case FETCH_PRICE_SUCCESS:
      return {
        ...state,
        pending: false,
        price: action.payload.price,
        error: null,
      };
    case FETCH_PRICE_FAILURE:
      return {
        ...state,
        pending: false,
        zip: [],
        error: action.payload.error,
      };
    default:
      return {
        ...state,
      };
  }
};
