import React, { FC } from "react";
import "./styles.scss";

type Props = {
  name: string;
  price: string;
  index: number;
  label?: string;
  onClick: (num: number) => void;
  distance?: any;
};

const TileItem: FC<Props> = ({ name, price, index, onClick, label, distance }) => {
  const onClickHandler = () => {
    onClick(index + 1);
  };
  return (
    <div className="tile-item">
      <p className="tile-item__name">{name}</p>
      {label && <p className="tile-item__label">{label}</p>}
      <div className="tile-item__content">
        <p className="tile-item__price">{price}</p>
        {distance && <p>{distance}</p>}
        <button className="tile-item__button" onClick={onClickHandler}>
          Get Coupon
        </button>
      </div>
    </div>
  );
};

export default TileItem;
