import axios from "axios";
import {
  fetchCurrentPharmFailure,
  fetchCurrentPharmSuccess,
} from "./currentPharmFetchActions";

import { all, call, put, takeLatest } from "@redux-saga/core/effects";
import { FETCH_CURRENT_PHARM_REQUEST } from "./currentPharmActions";
import { ICurrentPharm } from "./currentPharmTypes";

const getCurrentPharm: any = (name: string) =>
  axios({
    method: "POST",
    url: "http://localhost:5000/model",
    data: {
      pharm: name,
    },
  });

function* fetchCurrentPharmSaga(name: any): any {
  try {
    const response: any = yield call(getCurrentPharm, name.name);

    yield put(fetchCurrentPharmSuccess({ current: response.data }));
  } catch (e) {
    yield put(fetchCurrentPharmFailure({ error: e.message }));
  }
}

function* currentSaga() {
  yield all([takeLatest(FETCH_CURRENT_PHARM_REQUEST, fetchCurrentPharmSaga)]);
}

export default currentSaga;
