import { all, fork } from "redux-saga/effects";
import zipSaga from "./zipstore/zipSaga";
import allSaga from "./allPharm/allPharmSaga";
import priceSaga from "./price/priceSaga";
import currentSaga from "./currentPharm/currentPharmSaga";
export function* rootSaga() {
  yield all([fork(zipSaga), fork(allSaga), fork(priceSaga), fork(currentSaga)]);
}
