import {
  FETCH_CURRENT_PHARM_FAILURE,
  FETCH_CURRENT_PHARM_REQUEST,
  FETCH_CURRENT_PHARM_SUCCESS,
} from "./currentPharmActions";

import {
  FetchCurrentPharmFailure,
  FetchCurrentPharmSuccess,
  FetchCurrentPharmSuccessPayload,
  FetchCurrentPharmFailurePayload,
  FetchCurrentPharmRequest,
} from "./currentPharmTypes";

export const fetchCurrentPharmRequest = (
  name: string
): FetchCurrentPharmRequest => ({
  type: FETCH_CURRENT_PHARM_REQUEST,
  name,
});

export const fetchCurrentPharmSuccess = (
  payload: FetchCurrentPharmSuccessPayload
): FetchCurrentPharmSuccess => ({
  type: FETCH_CURRENT_PHARM_SUCCESS,
  payload,
});

export const fetchCurrentPharmFailure = (
  payload: FetchCurrentPharmFailurePayload
): FetchCurrentPharmFailure => ({
  type: FETCH_CURRENT_PHARM_FAILURE,
  payload,
});
