import React, { useEffect, useState } from "react";
import { getAllSelector } from "./store/allPharm/allPharmSelectors";
import { getZipSelector } from "./store/zipstore/zipSelectors";
import { fetchZipRequest } from "./store/zipstore/zipFetchActions";
import { fetchAllPharmRequest } from "./store/allPharm/allPharmFetchACtions";
import { useDispatch, useSelector } from "react-redux";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import ResultPage from "./pages/ResultPage";
import SearchPage from "./pages/SearchPage";
import "./assets/styles/app.scss";
const App: React.FC = () => {
  const allzip = useSelector(getZipSelector);
  const allPharm = useSelector(getAllSelector);

  const [searchValue, setSearchValue] = useState("");
  const [searchZip, setSearchZip] = useState("");
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchAllPharmRequest());
    if (searchZip.length > 0 && searchZip !== "") {
      dispatch(fetchZipRequest(searchZip));
    }
  }, [searchZip]);

  return (
    <Router>
      <div className="app">
        <Switch>
          <Route exact path="/results">
            <ResultPage />
          </Route>
          <Route path="/">
            <SearchPage
              searchValue={searchValue}
              allzip={allzip}
              searchZip={searchZip}
              allPharm={allPharm}
              setSearchZip={setSearchZip}
              setSearchValue={setSearchValue}
            />
          </Route>
        </Switch>
      </div>
    </Router>
  );
};
export default App;
