import React, { FC } from "react";

import { useHistory } from "react-router";
import { Footer, InputField } from "../../components";
import {
  HeaderLogoImage,
  LocationIconSvg,
  SearchIconSvg,
} from "../../assets/images";
import "./styles.scss";

interface IProps {
  searchValue: string;
  searchZip: string;
  setSearchValue: any;
  allPharm: any;
  setSearchZip: any;
  allzip: any;
  location?: string;
}

const SearchPage: FC<IProps> = ({
  searchValue,
  searchZip,
  allPharm,
  setSearchZip,
  setSearchValue,
  allzip,
}) => {
  const history = useHistory();

  const SubmitAction = () => {
    if (searchValue.length > 0 && searchZip.length > 0) {
      return history.push({
        pathname: "/results",
        state: { name: searchValue, location: allzip[0] },
      });
    }
  };

  return (
    <div className="search-page">
      <div>
        <div className="search-page__header">
          <img src={HeaderLogoImage} className="search-page__header-logo" />
        </div>
        <div className="search-page__content">
          <h1 className="search-page__title">
            Save on your medications today!
          </h1>
          <div className="search-page__input-wrapper">
            <InputField
              iconUrl={LocationIconSvg}
              value={searchZip}
              placeholder="ZIP"
              onChange={setSearchZip}
              haveSubmit
              onSubmit={SubmitAction}
              buttonText="Search ZIP"
              autocomplete={allzip}
            />
            <InputField
              iconUrl={SearchIconSvg}
              value={searchValue}
              placeholder="Search Medications"
              onChange={setSearchValue}
              haveSubmit
              onSubmit={SubmitAction}
              buttonText="Find lowest prices"
              autocomplete={allPharm}
            />
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default SearchPage;
