import { combineReducers } from "redux";
import zipReducer from "./zipstore/zipReducer";
import allPharmReducer from "./allPharm/allPharmReducer";
import priceReducer from "./price/priceReducer";
import currentPharmReducer from "./currentPharm/currentPharmReducer";
const rootReducer = combineReducers({
  zip: zipReducer,
  all: allPharmReducer,
  price: priceReducer,
  current: currentPharmReducer,
});

export type AppState = ReturnType<typeof rootReducer>;

export default rootReducer;
