import { createSelector } from "reselect";
import { AppState } from "../rootReducer";

const getPending = (state: AppState) => state.current.pending;
const getCurrent = (state: AppState) => state.current.current;
const getError = (state: AppState) => state.current.error;

export const getCurrentPharmSelector = createSelector(
  getCurrent,
  (current) => current
);
export const getCurrentPendingSelector = createSelector(
  getPending,
  (pend) => pend
);
export const getCurrentErrorSelector = createSelector(getError, (err) => err);
