import { createSelector } from "reselect";
import { AppState } from "../rootReducer";

const getPending = (state: AppState) => state.all.pending;
const getAll = (state: AppState) => state.all.all;
const getError = (state: AppState) => state.all.error;

export const getAllSelector = createSelector(getAll, (alls) => alls);
export const getAllPendingSelector = createSelector(getPending, (pend) => pend);
export const getAllErrorSelector = createSelector(getError, (err) => err);
