import {
  FETCH_ALL_FAILURE,
  FETCH_ALL_REQUEST,
  FETCH_ALL_SUCCESS,
} from "./allPharmActions";
import {
  FetchAllPharmFailure,
  FetchAllPharmfailurePayload,
  FetchAllPharmRequest,
  FetchAllPharmSuccess,
  FetchAllPharmSuccesspayload,
} from "./allPharmTypes";

export const fetchAllPharmRequest = (): FetchAllPharmRequest => ({
  type: FETCH_ALL_REQUEST,
});

export const fetchAllPharmSuccess = (
  payload: FetchAllPharmSuccesspayload
): FetchAllPharmSuccess => ({
  type: FETCH_ALL_SUCCESS,
  payload,
});

export const fetchAllPharmFailure = (
  payload: FetchAllPharmfailurePayload
): FetchAllPharmFailure => ({
  type: FETCH_ALL_FAILURE,
  payload,
});
