import React, { ChangeEvent, FC } from "react";
import AutocompleteInput from "../AutocompleteInput";

import "./styles.scss";

type Props = {
  classes?: string;
  autocompleteClasses?: string;
  placeholder?: string;
  value: string;
  iconUrl?: string;
  onChange: (value: string) => void;
  haveSubmit?: boolean;
  buttonText?: string;
  onSubmit?: (value: string) => void;
  // autocomplete?: Array<{
  //   label: string;
  // }>;
  autocomplete: any;
  ref?: any;
  child?: any;
};

const InputField: FC<Props> = ({
  classes,
  autocompleteClasses,
  placeholder = "",
  value,
  iconUrl,
  onChange,
  haveSubmit = false,
  buttonText,
  onSubmit,
  autocomplete = [],
  ref,
  child,
}) => {
  const submitAction = (value: string) => {
    if (onSubmit) onSubmit(value);
  };

  return (
    <div>
      <div className={`input-field ${classes}`}>
        <div className="input-field__row">
          <div className="input-field__icon-wrapper">
            <img src={iconUrl} className="input-field__icon" />
          </div>
          <div>
            <AutocompleteInput
              placeholder={placeholder}
              classes={
                autocompleteClasses === undefined
                  ? "input-field__input"
                  : autocompleteClasses
              }
              value={value}
              setValue={onChange}
              onSubmit={submitAction}
              autocomplete={autocomplete}
              ref={ref}
              child={child}
            />
          </div>
        </div>
        {haveSubmit && (
          <button
            className="input-field__submit-button"
            onClick={() => submitAction(value)}
          >
            {buttonText}
          </button>
        )}
      </div>
    </div>
  );
};

export default InputField;
