import { createSelector } from "reselect";
import { AppState } from "../rootReducer";

const getPending = (state: AppState) => state.price.pending;
const getPrices = (state: AppState) => state.price.price;
const getErrors = (state: AppState) => state.price.error;

export const getPriceSelector = createSelector(getPrices, (price) => price);
export const getPricePendingSelector = createSelector(
  getPending,
  (pending) => pending
);
export const getPriceErrorSelector = createSelector(
  getErrors,
  (errors) => errors
);
