import axios from "axios";

import { all, call, put, takeLatest } from "redux-saga/effects";
import { fetchZipFailure, fetchZipSuccess } from "./zipFetchActions";
import { FETCH_ZIP_REQUEST } from "./zipActions";
import { IZip } from "./zipTypes";
const getZip = (name: any) =>
  axios({
    method: "post",
    url: "http://localhost:5000/zips",
    data: {
      zip: name,
    },
  });

function* fetchZipSaga(name: any): any {
  try {
    const response = yield call(getZip, name.name);
    yield put(fetchZipSuccess({ zip: response.data }));
  } catch (e) {
    yield put(fetchZipFailure({ error: e.message }));
  }
}

function* zipSaga() {
  yield all([takeLatest(FETCH_ZIP_REQUEST, fetchZipSaga)]);
}

export default zipSaga;
