import {
  FETCH_ZIP_FAILURE,
  FETCH_ZIP_REQUEST,
  FETCH_ZIP_SUCCESS,
} from "./zipActions";
import { ZipActions, ZipState } from "./zipTypes";
const initialZipState: ZipState = {
  pending: false,
  error: null,
  zip: [],
};

export default (state = initialZipState, action: ZipActions) => {
  switch (action.type) {
    case FETCH_ZIP_REQUEST:
      return {
        ...state,
        pending: true,
      };
    case FETCH_ZIP_SUCCESS:
      return {
        ...state,
        pending: false,
        zip: action.payload.zip,
        error: null,
      };
    case FETCH_ZIP_FAILURE:
      return {
        ...state,
        pending: false,
        zip: [],
        error: action.payload.error,
      };
    default:
      return {
        ...state,
      };
  }
};
