import {
  FETCH_CURRENT_PHARM_FAILURE,
  FETCH_CURRENT_PHARM_REQUEST,
  FETCH_CURRENT_PHARM_SUCCESS,
} from "./currentPharmActions";

import { CurrentPharmActions, CurrentPharmState } from "./currentPharmTypes";

const initialCurrentPharmState: CurrentPharmState = {
  pending: false,
  error: null,
  current: [],
};

export default (
  state = initialCurrentPharmState,
  action: CurrentPharmActions
) => {
  switch (action.type) {
    case FETCH_CURRENT_PHARM_REQUEST:
      return {
        ...state,
        pending: true,
      };
    case FETCH_CURRENT_PHARM_SUCCESS:
      return {
        ...state,
        pending: false,
        current: action.payload.current,
        error: null,
      };
    case FETCH_CURRENT_PHARM_FAILURE:
      return {
        ...state,
        pending: false,
        current: [],
        error: action.payload.error,
      };
    default:
      return {
        ...state,
      };
  }
};
