import { createSelector } from "reselect";
import { AppState } from "../rootReducer";

const getPending = (state: AppState) => state.zip.pending;
const getZip = (state: AppState) => state.zip.zip;
const getErrors = (state: AppState) => state.zip.error;

export const getZipSelector = createSelector(getZip, (zips) => zips);
export const getZipPendingsSelector = createSelector(
  getPending,
  (pending) => pending
);
export const getZipErrorSelector = createSelector(
  getErrors,
  (errors) => errors
);
