import React, { FC } from "react";
import "./styles.scss";

type Props = {
  name: string;
  price: string;
  index: number;
  label?: string;
  onClick: (num: number) => void;
};

const RowItem: FC<Props> = ({ name, price, index, onClick, label }) => {
  const onClickHandler = () => {
    onClick(index + 1);
  };

  return (
    <div className="row-item">
      <div>
        <p className="row-item__name">{name}</p>
        {label && <p className="row-item__label">{label}</p>}
      </div>
      <div className="row-item__content">
        <p className="row-item__price">{price}</p>

        <button className="row-item__button" onClick={onClickHandler}>
          Get Coupon
        </button>
      </div>
    </div>
  );
};

export default RowItem;
