import axios from "axios";
import {
  fetchAllPharmFailure,
  fetchAllPharmSuccess,
} from "./allPharmFetchACtions";
import { all, call, put, takeLatest } from "redux-saga/effects";
import { FETCH_ALL_REQUEST } from "./allPharmActions";
import { IAllPharm } from "./allPharmTypes";

const getAllPharm = () => axios.get<IAllPharm[]>("http://localhost:5000/model");

function* fetchAllPharmSaga(): any {
  try {
    const response = yield call(getAllPharm);
    yield put(fetchAllPharmSuccess({ all: response.data }));
  } catch (e) {
    yield put(fetchAllPharmFailure({ error: e.message }));
  }
}

function* allSaga() {
  yield all([takeLatest(FETCH_ALL_REQUEST, fetchAllPharmSaga)]);
}

export default allSaga;
