import React, {
  FC,
  useEffect,
  useState,
  useMemo,
  useCallback,
  useRef,
} from "react";
import { useLocation, useHistory } from "react-router";
import { Link } from "react-router-dom";
import { getDistance, convertDistance } from "geolib";
import { useDispatch, useSelector } from "react-redux";
import { Loader, PlugSvg, RowItem, TileItem } from "../../components";
import {
  HeaderLogoImage,
  LocationIconSvg,
  SearchIconSvg,
} from "../../assets/images";
import { InputField, Dropdown, ItemsControl, Footer } from "../../components";
import { getAllSelector } from "../../store/allPharm/allPharmSelectors";
import { ITEMS_CONTROL_MODE } from "../../components/ItemsControl";
import { fetchAllPharmRequest } from "../../store/allPharm/allPharmFetchACtions";
import { fetchCurrentPharmRequest } from "../../store/currentPharm/currentPharmFetchActions";
import { getCurrentPharmSelector } from "../../store/currentPharm/currentPharmSelectors";
import { fetchPriceRequest } from "../../store/price/priceFetchACtions";
import { getPriceSelector } from "../../store/price/priceSelectors";
import "./styles.scss";

const ResultPage: FC = () => {
  const history = useHistory();
  const { state }: any = useLocation();

  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(fetchAllPharmRequest());
    dispatch(fetchCurrentPharmRequest(state.name));
  }, [state.name, history, dispatch]);

  const current: any = useSelector(getCurrentPharmSelector);
  const price = useSelector(getPriceSelector);
  const all = useSelector(getAllSelector);
  const mockSorting = ["LOWEST", "HIGHEST"];
  const [itemsMode, setItemsMode] = useState(ITEMS_CONTROL_MODE.TILES);
  const [searchValue, setSearchValue] = useState("");
  const [loading, setLoading] = useState(false);

  const [chosen, setChosen]: any = useState({
    locations: state.location.location,
    Manufacturer: "no items",
    Form: "no items",
    Dosage: "no items",
    Quantity: "no items",
  });

  const sorted: any = useMemo(() => {
    if (current.variants !== undefined && current.variants !== null) {
      const manufacturer: any = current.variants.map((e: any) => {
        return `${e.name} ${e.drugType}`;
      });

      const form: any = current.formulations
        .filter((e: any) => {
          return e.name === chosen.Manufacturer.split(" ")[0];
        })
        .map((e: any) => {
          return e.form;
        })
        .filter(function (value: any, index: any, self: any) {
          return self.indexOf(value.trim()) === index;
        });
      const dosage = current.formulations
        .filter((e: any) => {
          return e.name === chosen.Manufacturer.split(" ")[0];
        })
        .filter((e: any) => {
          return e.form === chosen.Form;
        })
        .map((e: any) => {
          return e.dosage;
        })
        .map((e: any) => {
          return e.display;
        });
      const quantity = current.formulations
        .filter((e: any) => {
          return e.name === chosen.Manufacturer.split(" ")[0];
        })
        .filter((e: any) => {
          return e.form === chosen.Form;
        })
        .filter((e: any) => {
          return e.dosage.display === chosen.Dosage;
        })
        .map((e: any) => {
          return e.commonQuantities;
        })
        .flat()
        .map((e: any) => {
          return e.display;
        });

      const formulationId = current.formulations
        .filter((e: any) => {
          return e.name === chosen.Manufacturer.split(" ")[0];
        })
        .filter((e: any) => {
          return e.form === chosen.Form;
        })
        .filter((e: any) => {
          return e.dosage.display === chosen.Dosage;
        })
        .map((e: any) => {
          return e.commonQuantities;
        })
        .flat()
        .filter((e: any) => {
          return e.display === chosen.Quantity;
        })
        .map((e: any) => {
          return e.ndc;
        });
      const value = current.formulations
        .filter((e: any) => {
          return e.name === chosen.Manufacturer.split(" ")[0];
        })
        .filter((e: any) => {
          return e.form === chosen.Form;
        })
        .filter((e: any) => {
          return e.dosage.display === chosen.Dosage;
        })
        .map((e: any) => {
          return e.commonQuantities;
        })
        .flat()
        .filter((e: any) => {
          return e.display === chosen.Quantity;
        })
        .map((e: any) => {
          return e.value;
        });

      const unique = Array.from(
        price
          ?.reduce((m: any, t: any) => m.set(t.retailer.name, t), new Map())
          .values()
      );

      const distance = unique
        .map((e: any) => {
          return getDistance(
            {
              lat: state.location.location.split(",")[0],
              lng: state.location.location.split(",")[1],
            },
            {
              lat: e.retailer.location.coordinates.latitude,
              lng: e.retailer.location.coordinates.longitude,
            }
          );
        })
        .map((e: any) => {
          return `${convertDistance(e, "mi").toFixed(2)} mi`;
        });

      return {
        manufacturer: manufacturer,
        form: form,
        dosage: dosage,
        quantity: quantity,
        formulationId: formulationId,
        value: value,
        unique: unique,
        distance: distance,
      };
    }
  }, [
    current,
    state.location.label,
    chosen.Manufacturer,
    chosen.Form,
    chosen.Dosage,
    chosen.Quantity,
    price,
  ]);

  const submitAction = () => {
    if (searchValue.length > 0) {
      return history.push({
        state: { name: searchValue, location: state.location },
      });
    }
  };

  useEffect(() => {
    if (
      sorted !== undefined &&
      sorted.formulationId[0] !== undefined &&
      sorted.value[0] !== undefined &&
      chosen.locations !== undefined
    ) {
      const payload = {
        formulationId: sorted.formulationId[0],
        quantity: sorted.value[0],
        location: chosen.locations,
      };

      dispatch(fetchPriceRequest(payload));
    }
  }, [sorted, chosen.locations]);

  // useEffect(() => {
  //   if (sorted) {
  //     setChosen((prev: any) => ({
  //       ...prev,
  //       Manufacturer:
  //         chosen.Manufacturer === "no items"
  //           ? sorted.manufacturer[0]
  //           : chosen.Manufacturer,
  //       Form:
  //         chosen.Form === "no items" || undefined
  //           ? sorted.form[0]
  //           : chosen.Form,
  //     }));
  //   }
  // }, [sorted]);
  return (
    <>
      {loading && (
        <div className="result-page__loader">
          <Loader />
        </div>
      )}
      {!loading && (
        <div className="result-page">
          <div className="result-page__header">
            <div className="result-page__row">
              <Link to="/">
                <img src={HeaderLogoImage} alt="" />
              </Link>
              <InputField
                value={searchValue}
                onChange={setSearchValue}
                placeholder="Type your drug name"
                iconUrl={SearchIconSvg}
                classes="result-page__header-input"
                autocompleteClasses="result-page__autocomplete"
                onSubmit={submitAction}
                autocomplete={all}
              />
            </div>
            <div className="result-page__row">
              <PlugSvg
                url={LocationIconSvg}
                classes="result-page__location-icon"
                title="location"
              />
              <p className="result-page__location-text">
                {state.location.label}
              </p>
            </div>
          </div>

          <div className="result-page__content">
            <h1 className="result-page__title">{state.name}</h1>
            <div className="result-page__separator--green" />

            {sorted && chosen && (
              <div className="result-page__dropdowns">
                <Dropdown
                  items={sorted.manufacturer}
                  chosen={chosen.Manufacturer}
                  setChosen={(e: any) =>
                    setChosen({
                      ...chosen,
                      Manufacturer: e,
                    })
                  }
                />
                <Dropdown
                  items={sorted.form}
                  chosen={
                    chosen.Form === undefined
                      ? setChosen({ ...chosen, Form: sorted.form[0] })
                      : chosen.Form
                  }
                  setChosen={(e: any) =>
                    setChosen({
                      ...chosen,
                      Form: e,
                    })
                  }
                />
                <Dropdown
                  items={sorted.dosage}
                  chosen={chosen.Dosage}
                  setChosen={(e: any) => setChosen({ ...chosen, Dosage: e })}
                />
                <Dropdown
                  items={sorted.quantity}
                  chosen={chosen.Quantity}
                  setChosen={(e: any) => setChosen({ ...chosen, Quantity: e })}
                />
              </div>
            )}
            <div className="result-page__items-control">
              <ItemsControl
                sortingItems={mockSorting}
                mode={itemsMode}
                action={setItemsMode}
              />
            </div>
            <div className="result-page__separator--grey" />

            {itemsMode === ITEMS_CONTROL_MODE.TILES && (
              <div className="result-page__tiles-area">
                <div className="result-page__tiles-wrapper">
                  {sorted !== undefined &&
                    sorted.unique !== undefined &&
                    sorted.distance !== undefined &&
                    sorted.unique.map((item: any, index: any) => (
                      <TileItem
                        key={item.retailer.name}
                        name={item.retailer.name}
                        price={item.price.display}
                        label=" Optum Perks"
                        index={index}
                        onClick={() => {}}
                        distance={`${sorted.distance[index]}`}
                      />
                    ))}
                </div>
              </div>
            )}
            {itemsMode === ITEMS_CONTROL_MODE.ROWS && (
              <div className="result-page__rows-wrapper">
                {sorted !== undefined &&
                  sorted.unique !== undefined &&
                  sorted.distance !== undefined &&
                  sorted.unique.map((item: any, index: any) => (
                    <RowItem
                      key={item.retailer.name}
                      name={item.retailer.name}
                      price={item.price.display}
                      label=" Optum Perks"
                      index={index}
                      onClick={() => {}}
                    />
                  ))}
              </div>
            )}
          </div>

          <div className="result-page__space-fill"> </div>
          <Footer />
        </div>
      )}
    </>
  );
};

export default ResultPage;
