import {
  FETCH_PRICE_FAILURE,
  FETCH_PRICE_REQUEST,
  FETCH_PRICE_SUCCESS,
} from "./priceActions";

import {
  FetchPriceFailure,
  FetchPriceFailurePayload,
  FetchPriceRequest,
  FetchPriceSuccess,
  FetchPriceSuccessPayload,
} from "./priceTypes";

export const fetchPriceRequest = (payload: any): FetchPriceRequest => ({
  type: FETCH_PRICE_REQUEST,
  payload,
});

export const fetchPriceSuccess = (
  payload: FetchPriceSuccessPayload
): FetchPriceSuccess => ({
  type: FETCH_PRICE_SUCCESS,
  payload,
});

export const fetchPriceFailure = (
  payload: FetchPriceFailurePayload
): FetchPriceFailure => ({
  type: FETCH_PRICE_FAILURE,
  payload,
});
