import {
  FETCH_ZIP_FAILURE,
  FETCH_ZIP_REQUEST,
  FETCH_ZIP_SUCCESS,
} from "./zipActions";
import {
  FetchZipFailure,
  FetchZipFailurePayload,
  FetchZipRequest,
  FetchZipSuccess,
  FetchZipSuccessPayload,
} from "./zipTypes";

export const fetchZipRequest = (name: any): FetchZipRequest => ({
  type: FETCH_ZIP_REQUEST,
  name,
});

export const fetchZipSuccess = (
  payload: FetchZipSuccessPayload
): FetchZipSuccess => ({
  type: FETCH_ZIP_SUCCESS,
  payload,
});

export const fetchZipFailure = (
  payload: FetchZipFailurePayload
): FetchZipFailure => ({
  type: FETCH_ZIP_FAILURE,
  payload,
});
