import {
  FETCH_ALL_FAILURE,
  FETCH_ALL_REQUEST,
  FETCH_ALL_SUCCESS,
} from "./allPharmActions";

import { AllPharmState, AllPharmActions } from "./allPharmTypes";

const initializeAllPharmState: AllPharmState = {
  pending: false,
  error: null,
  all: [],
};

export default (state = initializeAllPharmState, action: AllPharmActions) => {
  switch (action.type) {
    case FETCH_ALL_REQUEST:
      return {
        ...state,
        pending: true,
      };
    case FETCH_ALL_SUCCESS:
      return {
        ...state,
        pending: false,
        all: action.payload.all,
        error: null,
      };
    case FETCH_ALL_FAILURE:
      return {
        ...state,
        pending: false,
        all: [],
        error: action.payload.error,
      };
    default:
      return {
        ...state,
      };
  }
};
