import axios from "axios";
import { all, call, put, takeLatest } from "@redux-saga/core/effects";
import { fetchPriceFailure, fetchPriceSuccess } from "./priceFetchACtions";
import { FETCH_PRICE_REQUEST } from "./priceActions";
import { IPrice } from "../../../../model/price.model";

const getPrice = async (payload: any) => {
  const result = await axios({
    method: "POST",
    url: "http://localhost:5000/price",
    data: {
      quantity: payload.quantity,
      formId: payload.formulationId,
      location: payload.location,
    },
  });
  return result;
};

function* fetchPriceSaga(payload: any): any {
  try {
    const response = yield call(getPrice, payload.payload);
    console.log(response);
    yield put(fetchPriceSuccess({ price: response.data.data }));
  } catch (e) {
    yield put(fetchPriceFailure({ error: e.message }));
  }
}

function* priceSaga() {
  yield all([takeLatest(FETCH_PRICE_REQUEST, fetchPriceSaga)]);
}

export default priceSaga;
